export default {
	data() {
		return {
			downApp: false,
			blogInfo: {},
			commentsInfo: [],
			token: '',
			onCommentHot: true,
		}
	},
	onLoad: function(option) { //option为object类型，会序列化上个页面传递的参数
		let self = this;
		uni.getStorage({
			key: 'user_info',
			success: function(res) {
				self.token = res.data.token
			}
		})
		if (option.id) {
			this.getBlogInfo(option.id)
			if (this.token) {
				this.clickAdd(option.id);
			}
		} else {
			uni.navigateBack({
				delta: 1
			})
		}
	},
	methods: {
		getBlogInfo(id) {
			uni.request({
				url: this.$serverUrl + '/blog/detail',
				data: {
					id: Number(id)
				},
				header: {
					"Content-Type": "application/x-www-form-urlencoded",
					"authorization": this.token
				},
				method: "POST",
				success: (res) => {
					if (res.data.code !== 0) {
						uni.showModal({
							content: '请求失败，失败原因：' + res.data.msg,
							showCancel: false
						})
						return;
					}
					console.log(res.data.data);
					this.blogInfo = res.data.data;
					this.commentsInfo = this.blogInfo.comments;
					this.downApp = true;
				},
				fail: () => {
					uni.showModal({
						content: '点击接口调用失败!',
						showCancel: false
					})
				}
			})
		},
		clickAdd(id) {
			uni.request({
				url: this.$serverUrl + '/blog/click',
				data: {
					id: Number(id)
				},
				header: {
					"Content-Type": "application/x-www-form-urlencoded",
					"authorization": this.token
				},
				method: "POST",
				success: (res) => {
					if (res.data.code !== 0) {
						uni.showModal({
							content: '请求失败，失败原因：' + res.data.msg,
							showCancel: false
						})
						return;
					}
				},
				fail: () => {
					uni.showModal({
						content: '点击接口调用失败!',
						showCancel: false
					})
				}
			})
		},
		onComment() {
			this.onCommentHot = true;
		},
		onHot() {
			this.onCommentHot = false;
		}
	}
}
